  .flip-container {
    -webkit-perspective: 1000px;
            perspective: 1000px;
  }
  
  .cardBackdrop {
    position:fixed;
    padding:0;
    margin:0;
    top:0;
    left:0;
    width: 100%;
    height: 100%;
    z-index: -1;
  }
  
  
  .flip-container:hover .flipper, .flip-container.hover .flipper {
  
      -webkit-transform: rotateY(180deg);
  
              transform: rotateY(180deg);
    }
  
  .flip-container, .card {
    width: 296px;
    height: 456px;
    position: absolute;
    top: 53%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }
  
  
  .flipper {
    -webkit-transition: 0.6s;
    -o-transition: 0.6s;
    transition: 0.6s;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
  
    position: relative;
  }
  
  .card {
  
  -webkit-backface-visibility: hidden;
  
          backface-visibility: hidden;
  overflow: hidden;
    position: absolute;
    top: 0;
    left: 0;
    /* border-radius: 20px; */
    -webkit-box-shadow: 1px 2px 4px 5px  rgba(0, 0, 0, 0.4);
            box-shadow: 1px 2px 4px 5px  rgba(0, 0, 0, 0.4);
    background: white;
  }
  
  .card > img {
    height: auto;
    width: 274px;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }
  

  @media(min-width: 320px) and (max-width: 500px){
      .card > img{
        height: auto;
        width: 165px;
        position: absolute;
        top: 50%;
        left: 50%;
      }
      .card{
        width: 195px;
        height: 288px;
        left: 15%;
      }

      .flip-container{
        top: 66%;
      }
      
      .cardBackdrop{
        width: 200%;
        left: -150px;
      }
      
  }
  
  
  .card--front {
    z-index: 2;
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
  }
  
  .card--back {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg); 
  }
  

  .card-text{
    position: absolute;
    top: 40%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
        -ms-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    font-size: 20px;
    text-align: center;

  }
  
  



