.headerMenu{
  position: fixed;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  background: white;
  z-index: 100;
  top: 0;
  justify-items: center;
  -ms-flex-pack: end;
      justify-content: flex-end;
  height: 71px;
}


.headerMenu--list{
  -ms-flex-order: 1;
      order: 1;
  display: -ms-flexbox;
  display: flex;
  list-style-type: none;
  -ms-flex-item-align: center;
      align-self: center;
  position: absolute;
  left: 6%;

}

.smallLogoIllustrationHeader{
  -ms-flex-order: 2;
      order: 2;
  height: 45px;
}



/*MAIN NAV STYLING*/
a{
  text-decoration: none;
  color: gray;
}

a:hover{
  font-family: 'camptonBold';
}

.is-active{
  font-family: 'camptonBold';
  color: black;
}



.smallLogoTextHeader{
  margin-top: 11px;
  margin-right: 10px;
  height: 50px;
  width: auto;
  -ms-flex-order: 3;
      order: 3;

}




.headerMenu--item{
  padding: 10px
}

@media(max-width: 499px){
  .headerMenu--list{
    display: none;
  }
}

@media(min-width: 500px){
  .smallLogoIllustrationHeader{
    -ms-flex-order: 1;
        order: 1;
    position: absolute;
    left: 0;
    height: 37px;
    top: -3px;

  }
  .headerMenu--list{
    -ms-flex-order: 2;
        order: 2;
  }
}



